body {
  background-image: url("images/aoc_logo_bw.png");
  background-repeat: repeat;
  background-size: 300px;
  overflow-y: scroll;
}

.card-footer {
  background: transparent;
  border-top: 0px;
}

.jumbotron-image {
  background: url("images/doctors-banner.jpg") no-repeat fixed;
}

.jumbotron {
  box-shadow: inset 0 0 0 100vw rgba(0, 0, 0, 0.1);
  height: 40vh;
  background-size: cover;
}

.bg-primary {
  background-color: red !important;
}

.btn-primary {
  background-color: red;
  border-color: red;
}

.btn-primary:hover {
  background-color: darkred;
  border-color: darkred;
}

.btn-primary:focus,
.btn-primary.focus {
  background-color: darkred;
  border-color: darkred;
  box-shadow: 0 0 0 0.2rem rgba(165, 147, 194, 0.5);
}

.btn-primary.disabled,
.btn-primary:disabled {
  background-color: red;
  border-color: red;
}

.btn-primary:not(:disabled):not(.disabled):active,
.btn-primary:not(:disabled):not(.disabled).active,
.show > .btn-primary.dropdown-toggle {
  background-color: darkred;
  border-color: darkred;
}

.btn-primary:not(:disabled):not(.disabled):active:focus,
.btn-primary:not(:disabled):not(.disabled).active:focus,
.show > .btn-primary.dropdown-toggle:focus {
  box-shadow: 0 0 0 0.2rem rgba(165, 147, 194, 0.5);
}

.btn-outline-primary {
  color: red;
  border-color: red;
}

.btn-outline-primary:hover {
  background-color: red;
  border-color: red;
}

.btn-outline-primary:focus,
.btn-outline-primary.focus {
  box-shadow: 0 0 0 0.2rem rgba(165, 147, 194, 0.5);
}

.btn-outline-primary.disabled,
.btn-outline-primary:disabled {
  color: red;
}

.btn-outline-primary:not(:disabled):not(.disabled):active,
.btn-outline-primary:not(:disabled):not(.disabled).active,
.show > .btn-outline-primary.dropdown-toggle {
  background-color: red;
  border-color: red;
}

.btn-outline-primary:not(:disabled):not(.disabled):active:focus,
.btn-outline-primary:not(:disabled):not(.disabled).active:focus,
.show > .btn-outline-primary.dropdown-toggle:focus {
  box-shadow: 0 0 0 0.2rem rgba(165, 147, 194, 0.5);
}

.text-primary {
  color: red !important;
}

.text-secondary {
  color: #6c757d !important;
}

a.text-secondary:hover,
a.text-secondary:focus {
  color: #494f54 !important;
}

.border-primary {
  border-color: red !important;
}

.breadcrumb a {
  color: darkred;
}

.accordion-button:not(.collapsed) {
  color: darkred;
  background-color: #d8c1ff;
}

.dropdown-item.active,
.dropdown-item:active {
  background-color: red;
}

.form-check-input:checked {
  border-color: red;
  background-color: red;
}

.navbar-dark .navbar-nav .nav-link {
  color: rgba(255, 255, 255, 0.65);
}
